
.footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.footer a.link {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
}
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 48px;
  border-top: 1px solid #ccc;
  background: #fff;
}
.footer a.link {
    line-height: 48px;
    text-align: center;
    font-size: 20px;
}
.footer a.link:first-child {
      border-right: 1px solid #ccc;
}
.footer a.link.on {
      color: #80bd01;
}



