
.modal-foot {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.modal-foot .btn {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
}
#mask {
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.modal-wrapper {
  z-index: 100;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 280px;
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-sizing: border-box;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.modal-head {
    padding: 15px 0 5px;
    text-align: center;
    font-size: 20px;
}
.modal-body {
    padding: 10px 15px;
    text-align: justify;
    font-size: 16px;
}
.modal-foot {
    height: 40px;
    border-top: 1px solid #ccc;
}
.modal-foot .btn {
      line-height: 40px;
      text-align: center;
      color: #666;
}
.modal-foot .btn:first-child {
        border-right: 1px solid #ccc;
}
.modal-foot .btn.btn-sure {
        color: #80bd01;
}



