
.detail-head, .comment-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.detail-head .user-info, .comment-info {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
}
.detail-head .btn-keep, .comment-wrapper .btn-good, .new-comment .cont {
  box-sizing: border-box;
}
.detail-article {
  background: #fff;
}
.detail-head {
  padding: 10px 12px;
  border-bottom: 1px solid #ccc;
  background: #fff;
}
.detail-head .user-avatar {
    width: 50px;
    height: 50px;
    background: url("../assets/images/avatar.png") no-repeat;
    background-size: 100% auto;
    border-radius: 50%;
    overflow: hidden;
}
.detail-head .user-avatar img {
      display: block;
      width: 50px;
      height: 50px;
}
.detail-head .user-info {
    padding: 0 10px;
}
.detail-head .user-info .name {
      margin: 5px 0 0;
}
.detail-head .user-info .name .type {
        display: inline-block;
        padding: 0 5px;
        margin-left: 5px;
        font-size: 12px;
        color: #80bd01;
        border: 1px solid #80bd01;
        border-radius: 5px;
}
.detail-head .user-info .time {
      margin: 5px 0 0;
      font-size: 12px;
      color: #666;
}
.detail-head .user-info .time span {
        color: #999;
}
.detail-head .btn-keep {
    display: block;
    height: 30px;
    line-height: 30px;
    text-align: center;
    margin: 10px 0;
    padding: 0 10px;
    font-size: 16px;
    color: #fff;
    background: #80bd01;
    border-radius: 5px;
}
.detail-head .btn-keep.on {
      color: #fff;
      background: #e1e1e1;
}
.detail-title {
  position: relative;
  padding: 10px 12px;
  font-size: 18px;
  font-weight: 500;
}
.detail-title:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 12px;
    right: 12px;
    border-bottom: 1px dashed #ccc;
}
.detail-content {
  padding: 0 12px;
  color: #000;
  line-height: 1.6;
}
.detail-content img {
    max-width: 100%;
}
.detail-comment {
  padding: 10px 0 0;
  background: #f1f1f1;
}
.detail-comment h1.title {
    margin: 0;
    font-weight: normal;
    padding: 8px 12px;
    font-size: 18px;
    border-bottom: 1px solid #ccc;
    background: #fff;
}
.comment-wrapper {
  position: relative;
  padding: 10px 12px;
  background: #fff;
}
.comment-wrapper:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 12px;
    right: 0;
    height: 1px;
    background: #ccc;
}
.comment-wrapper img {
    max-width: 100%;
}
.comment-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: url("../assets/images/avatar.png") no-repeat;
    background-size: 40px auto;
    overflow: hidden;
}
.comment-avatar img {
      display: block;
      width: 40px;
      height: 40px;
}
.comment-info {
    padding: 0 0 0 10px;
}
.comment-info p {
      margin: 0;
}
.comment-info .name {
      padding: 5px 0 0;
}
.comment-info .time {
      font-size: 12px;
      color: #666;
}
.comment-info .content {
      padding-top: 5px;
}
.comment-wrapper .btn-good {
    position: absolute;
    top: 5px;
    right: 10px;
    height: 40px;
    line-height: 40px;
    text-align: right;
    padding: 0 0 0 20px;
    background: url("../assets/images/icon_like.png") no-repeat left center;
    background-size: 20px auto;
}
.comment-wrapper .btn-good.on {
      background-image: url("../assets/images/icon_like_on.png");
}
.comment-none {
  padding: 15px 0;
  text-align: center;
  font-size: 16px;
  color: #999;
}
.markdown-text a {
  color: #80bd01;
}
.markdown-text pre {
  white-space: normal;
}
.new-comment {
  padding: 10px 12px;
  background: #fff;
}
.new-comment:after {
    content: '';
    display: block;
    height: 0;
    clear: both;
}
.new-comment .cont {
    display: block;
    width: 100%;
    height: 150px;
    padding: 10px;
    font-size: 16px;
    border: 0;
    background: #efecec;
}
.new-comment .btn {
    display: block;
    float: right;
    width: 100px;
    height: 40px;
    margin-top: 10px;
    line-height: 40px;
    text-align: center;
    font-size: 18px;
    color: #fff;
    border-radius: 5px;
    border: 0;
    background: #80bd01;
}



