
.tab-nav, .topics-list li a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tab-nav a.btn, .topic-desc {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
}
.tab-nav {
  position: fixed;
  top: 36px;
  left: 0;
  width: 100%;
  background: #333;
}
.tab-nav a.btn {
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    color: #80bd01;
}
.tab-nav a.btn.active {
      color: #fff;
}
.tab-content {
  padding: 40px 0 49px;
}
.topics-list li a {
  padding: 12px 10px;
  margin-bottom: 10px;
  background: #fff;
}
.topic-man {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: url(../assets/images/avatar.png) no-repeat;
    background-size: 40px auto;
    overflow: hidden;
}
.topic-man img {
      display: block;
      width: 40px;
      height: 40px;
}
.topic-desc {
    padding-left: 10px;
}
.topic-desc .name {
      margin: 0;
}
.topic-desc .name .time {
        float: right;
        font-size: 12px;
        color: #666;
}
.topic-desc .title {
      margin: 5px 0 8px;
      font-size: 16px;
      text-align: justify;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      text-overflow: ellipsis;
      overflow: hidden;
}
.topic-desc .title .type {
        display: inline-block;
        padding: 0 5px;
        border-radius: 5px;
        margin-right: 5px;
        font-size: 12px;
        color: #f90;
        border: 1px solid #f90;
        border-radius: 5px;
}
.topic-desc .summary {
      margin: 0;
      color: #999;
}
.topic-desc .summary .type {
        display: inline-block;
        padding: 0 5px;
        border-radius: 5px;
        font-size: 12px;
        color: #80bd01;
        border: 1px solid #80bd01;
        border-radius: 5px;
}
.topic-desc .summary .read, .topic-desc .summary .comment {
        float: right;
        display: block;
        padding-left: 20px;
        background-repeat: no-repeat;
        background-position: left center;
        background-size: 17px auto;
}
.topic-desc .summary .read {
        background-image: url(../assets/images/icon_read_34x34.png);
}
.topic-desc .summary .comment {
        margin-left: 10px;
        background-image: url(../assets/images/icon_comment_2.png);
}
.load-result {
  display: none;
  padding: 10px 0;
  margin-top: -10px;
  color: #666;
  text-align: center;
}



